export type TimeFormat = "date" | "time" | "datetime"| "datepath";

export enum LogLevel {
    debug = "DEBUG",
    info= "INFO",
    log = "LOG",
    trace = "TRACE",
    warn = "WARNING",
    error = "ERROR"
}
export const LogLevelLookup = {
    DEBUG: 10,
    INFO:20,
    LOG: 30,
    TRACE: 40,
    WARNING: 50,
    ERROR: 60,
}

export interface ILogItem {
    /** Log message */
    message: any;
    /** Log level */
    level?: LogLevel;
    /**  */
    error?: unknown;
    /** Inserts the current time in your chosen format before the message */
    timeFormat?: TimeFormat;
    /** For additional info */
    additional?: any;
    /** For writing disk */
    diskPermission?:boolean;
    /** For writing disk path */
    logDiskPath?:string;
}